import * as S from "./styles";

interface propsType {
  title?: string;
  content?: string;
  imgURL?: string;
}

export default function Post(props: propsType) {
  const { title, content, imgURL } = props;
  return (
    <div>
      <S.PostImage>
        <img src={imgURL ? imgURL : ""} alt="" />
        <S.imageTitle>{title ? title : " "}</S.imageTitle>
      </S.PostImage>
      <S.P>{content ? content : " "}</S.P>
    </div>
  );
}
